import "core-js/modules/es.string.includes";
import "core-js/modules/es.string.split";
import "core-js/modules/es.string.trim";
import "core-js/modules/es.string.includes";
import "core-js/modules/es.string.split";
import "core-js/modules/es.string.trim";
/**
 * Layout: Checkout
 * -----------------------------------------------------------------------------
 * Entry file for all checkout scripts.
 *
 */

import '../../styles/checkout.scss';
import checkoutCount from '../components/checkout-cart-count';
import { misc } from '../helpers/svg-map';
let loaded = false;
/**
 * Add info icon to footer after checkout.
 */

function addInfoIcon() {
  const helpText = document.querySelector('.step__footer__info');
  const newSpan = document.createElement('span');
  newSpan.innerHTML = misc.info;
  helpText.prepend(newSpan);
}
/**
 * Add item count to order summary after checkout.
 */


function addOrderItemCount() {
  const count = document.querySelector('.order-count');
  const toggleIcon = document.querySelector('.order-summary-toggle__icon-wrapper');
  toggleIcon.innerHTML += "\n    <span class=\"order-summary-toggle__count\" js-checkout=\"count\">\n      ".concat(count.innerHTML.trim(), "\n    </span>\n  ");
}
/**
 * Set variant details inside Order Summary section of checkout page.
 * Remove variant color, but keep variant size.
 * Old: `DARK BROWN / 5.5`
 * New: `Size: 5.5`
 */


function setVariantDetails() {
  const items = document.getElementsByClassName('product__description__variant');

  for (let i = 0; i < items.length; i++) {
    const variantDetails = items[i].innerHTML;

    if (variantDetails.includes('/')) {
      const variantSize = variantDetails.split('/')[1];
      items[i].innerHTML = "".concat(theme.strings.size, " ").concat(variantSize);
    }
  }
}

['DOMContentLoaded', 'page:load', 'page:change', 'page:update'].forEach(event => {
  document.addEventListener(event, () => {
    // Load these functions once.
    if (!loaded) {
      if (Shopify.Checkout.isOrderStatusPage || Shopify.Checkout.step === 'thank_you') {
        addInfoIcon();
        addOrderItemCount();
      } else {
        setVariantDetails();
        checkoutCount().init();
      }
    }

    loaded = true;
  });
});